@import "../../../sass/formAnimation.scss";

.modalDeleteForm {
  position: relative;
  padding: 15px;
  background-color: var(--main-background-color-alpha);
  width: 100%;
  max-width: 450px;
  border: 1px solid var(--color-accent);
  border-radius: 5px;
  transition: background-color 250ms linear;
  @include form-animation-settings;

  & h2 {
    text-align: center;
    margin-bottom: 20px;
  }
}

.text {
  text-align: center;
  font-size: 18px;
  margin-bottom: 20px;
}

.buttonWrapper {
  display: flex;
  justify-content: space-around;
}
